.main {
     height: 100vh;
     background-color: #1f1f20;
     display: flex;
    justify-content: center;
    padding: 100px 0;
 }

.container {
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.greeting{
    display: flex;
    flex-direction: column;
}
.greeting span:nth-child(1) {
    font-size: 16px;
}
.greeting span:nth-child(2),
.greeting span:nth-child(2) span{
    font-weight: 700;
    font-stretch: normal;
    font-size: 50px;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
    position: relative;
    z-index: 0;
}
.greeting span:nth-child(2) span:before{
content: "";
position: absolute;
  width: 100%;
    height: 20px;
    background-color:  #ea548d;
    bottom: 0px;
    z-index: -1;
}
h1{
    font-size: 27px;
}

.photo{
    margin-top: 50px;
    position: relative;
    z-index: 1;
}
.image {
    height: 400px;
    width: 300px;
    background-color: #a9a9a9;

}
.photo .image:before{
    content: "";
    position: absolute;
    height: 430px;
    width: 300px;
    border: 5px solid  #ea548d;
    left: 15px;
    top: -19px;
    z-index: -1;
}