.footer{
    height: 250px;
    background-color: #252527;
    display: flex;
    justify-content: center;
}
.container{
    /*border: 1px solid;*/
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
}
.socialBlock{
    display: flex;
    justify-content: space-between;
    width: 500px;
    border: 1px solid;
}
.socialIcon{
    width: 60px;
    height: 60px;
    background-color: #dfc1ff;
}
@media screen and (max-width: 626px) {
    .formWrapper {
        width: 100%;
    }
}
@media screen and (max-width: 340px) {
    .socialIcon {
        width: 50px;
        height: 50px;
    }
}
