.contacts{
    height: 100vh;
    background-color: #252527;
    display: flex;
    justify-content: center;
}
.container{
    /*border: 1px solid;*/
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
}
.formWrapper{
    display: flex;
    flex-direction: column;
    width: 500px;
}
.btnSubmit{
    height: 150px;
    width: 50px;
    background-color: #9180d2;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    border: none;
}
.messageArea{
    min-height: 160px;
}
@media screen and (max-width: 626px){
    formWrapper{
        width: 100%;
    }
}